'use strict';

/**
* Flatten a shape starting from a specified dimension.
*
* @module @stdlib/ndarray-base-flatten-shape-from
*
* @example
* import flattenShapeFrom from '@stdlib/ndarray-base-flatten-shape-from';
*
* var sh = flattenShapeFrom( [ 3, 3, 2 ], 1 );
* // returns [ 3, 6 ]
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import assign from './assign.js';


// MAIN //

setReadOnly( main, 'assign', assign );


// EXPORTS //

export default main;

// exports: { "assign": "main.assign" }
